<template>
    <v-container>
      <v-row>
        <v-col>
          <p v-html="formattedText"></p>
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
  export default {
    name: 'saleTalk',
    data() {
      return {
        currentContact: null, // Assuming you receive 'currentContact' from navigation
        formattedText: ""
      };
    },
    created() {
      // Extract project and name from route query parameters (if any)

      
   
      const contactString = this.$route.query.contact;
      const contact = JSON.parse(contactString);
      var name = "";
      var project = "";
    if (contact) {
       name = contact.name;
       project = contact.project;
       if (contact.contacted == "0")
       {
       this.formattedText = `<b>@project: Your Gateway to Urban Living</b><br/>
          Previously, you inquired about <b>Ashwood</b>, our newly launched condominium at Jalan Uthant. Strategically located just 5 minutes from KLCC and 10 minutes from TRX, Ashwood offers the perfect blend of convenience and tranquility.<br/><br/>
          Nestled amidst prestigious educational institutions like Flose International School and SJKK Chung Hwa(p) School, @project provides a prime location for families and individuals seeking a high-quality lifestyle.<br/><br/>
  
          <b>Exclusive Event This Saturday</b><br/>
          Join us this Saturday for a private event where you can explore our remaining 30 units. All units are corner units, ensuring breathtaking views of KLCC or Bukit Ampang.<br/>
          Whether you're looking for a dream home or a lucrative investment, @project presents an exceptional opportunity.<br/><br/>
  
          <b>Reseller Program</b><br/>
          We invite you to participate in our reseller program and earn referral fees for every successful sale.<br/><br/>
  
          <b>Unit at @project</b><br/>
          Hi @name, how are you today? We understand that you have a unit at @project. Are you considering renting or selling it?<br/>
          We offer complimentary advertising services to help you generate income from your property.`
       }
       else
       this.formattedText = `<b>@project: Your Gateway to Urban Living</b><br/>
          Just to follow up with you from our recent call, can I invite you to come to 
          Join us this Saturday for a private viewing where you can explore our remaining 30 units. All units are corner units, ensuring breathtaking views of KLCC or Bukit Ampang.<br/>
          Whether you're looking for a dream home or a lucrative investment, Ashwood presents an exceptional opportunity.<br/><br/>
  
          <b>Reseller Program</b><br/>
          We invite you to participate in our reseller program and earn referral fees for every successful sale.<br/><br/>
  
          <b>Unit at @project</b><br/>
          Hi @name, how are you today? We understand that you have a unit at @project. Are you considering renting or selling it?<br/>
          We offer complimentary advertising services to help you generate income from your property.`


    }
      if (project && name) {
        // Update formattedText with extracted data and currentContact details
        this.formattedText = this.buildPersonalizedText(project, name);
      } 
    },
    methods: {
      buildPersonalizedText(project, name) {
        // Modify the text to personalize based on project and name
        let personalizedText = this.formattedText;
        personalizedText = personalizedText.replaceAll('@name', name); // Replace placeholder with name
        personalizedText = personalizedText.replaceAll('@project', project); // Replace placeholder with name
        // Add custom logic to personalize based on project (if needed)
        return personalizedText;
      }
    }
  };
  </script>
  
  <style scoped>
  p {
    white-space: pre-wrap;
  }
  </style>